import React, { useState, useEffect } from "react";
import axios from "axios";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
function Slick() {
  var settings = {
    dots: true,
    infinite: true,
    autoplay: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
          dots: true
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          initialSlide: 2
        }
      },
      {
        breakpoint: 580,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  };
  // const [data, setData] = useState({ data1: [] });
  const [datac, setDatac] = useState({ banners: [] });

  useEffect(() => {
    const fetchDatac = async () => {
      const result = await axios(
        // "https://hn.algolia.com/api/v1/search?query=redux"
        // "https://f6b40b59-cb39-478b-b8b6-5784257bfcd3.mock.pstmn.io/creatuser1"
        "https://fakewoocomapi.github.io/nested_multi/"
      );

      setDatac(result.data);
    };

    fetchDatac();
  }, []);

  return (
    <ul className="slick_pic p-0">
      {/* {data.data1.map((item) => ( */}
      <Slider {...settings}>
        {datac.banners.map((item) => (
          <li key={item.objectID}>
            <p>
              <img src={item.image} alt="pic abc" />{" "}
            </p>
            <p>{item.id} </p>
            <p>{item.name} </p>
            <p>{item.first_name} </p>

            <p>{item.email} </p>
          </li>
        ))}
      </Slider>
    </ul>
  );
}

export default Slick;
