import { addToCart } from "../slices/cartSlice";
import { useGetAllProductsQuery } from "../slices/productsApi";
import { Link } from "react-router-dom";

const Productlist = () => {
  const { items: products, status } = useSelector((state) => state.products);
  const dispatch = useDispatch();
  const history = useHistory();

  const { data, error, isLoading } = useGetAllProductsQuery();
  console.log("Api", isLoading);

  const handleAddToCart = (product) => {
    dispatch(addToCart(product));
    history.push("/cart");
    // history.push("/details");
  };

  return (
    <div className="container">
      <div className="row">
        {status === "success" ? (
          <>
            <h2>New Arrivals</h2>
            <div className="zproducts row">
              {data &&
                data?.map((product) => (
                  <div
                    key={product.id}
                    className="zproduct col-md-4 col-6 mt-4 "
                  >
                    <div className="card2">
                      <Link
                        className="btn noborder-dark rounded-0 d-block"
                        to={`/cart2/${product.id}`}
                      >
                        <div className="picbox text-center btn_line w-100 ">
                          <img src={product.image} alt={product.title} />
                        </div>
                      </Link>
                      <div className="p-2">
                        <h3>{product.title}</h3>

                        <span className="price">$ {product.price}</span>

                        <div className="d-flex justify-content-between mt-2 mb-2">
                          <Link
                            className="btn border-dark noborder-dark rounded-0 btn_line"
                            to={`/cart2/${product.id}`}
                          >
                            {/* <FontAwesomeIcon
                              className="cu_fnt-awsme"
                              icon={faEye}
                            />{" "} */}
                            View
                          </Link>
                          <button
                            className="btn btn-dark rounded-0"
                            onClick={() => handleAddToCart(product)}
                          >
                            <img
                              className="ad_crt_icn"
                              src="https://fakewoocomapi.github.io/images/sh_ic1.png"
                              alt="Fitted shirt"
                            />
                            Add To Cart
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
            </div>
          </>
        ) : status === "pending" ? (
          <p>Loading...</p>
        ) : (
          <p>Unexpected error occured...</p>
        )}
      </div>
    </div>
  );
};

export default Productlist;
